import { Component, OnInit } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {

  constructor(
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
  }
  // ngAfterViewInit() {
  //   this.vhAlgorithm.waitingStack().then(() => {
  //     this.price = this.vhAlgorithm.vhnumeral(".price");
  //   });
  // }
  // price: any;
  list_products: any = [
    {
      name: 'Bánh Choco',
      quantity: 1,
      price: 15000
    },
    {
      name: 'Thay màn hình',
      quantity: 1,
      price: 15000
    }
  ];

  /**Search Products */
  visible: boolean = false;
  open(): void {
    this.visible = true;
  }


  close(): void {
    this.visible = false;
  }

  /**Show modal edit price */
  isVisibleEditPrice = false;

  showModal(): void {
    this.isVisibleEditPrice = true;
  }

  handleOk(): void {
    this.isVisibleEditPrice = false;
  }

  handleCancel(): void {
    this.isVisibleEditPrice = false;
  }

  /**after choose product */
  addListProduct(newItem) {
    let data = { ...newItem };
    data.quantity = 1;
    this.list_products.push(data);
  }
  closeSearch(status) {
    this.visible = status;
  }

  /**Xóa sản phẩm đã chọn */
  deleteItem(item) {
    let index = this.list_products.findIndex(product => product == item);
    this.list_products.splice(index, 1);
  }

}
