import { Component, OnInit, Output, EventEmitter, Input, ElementRef, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'app-search-product',
  templateUrl: './search-product.component.html',
  styleUrls: ['./search-product.component.scss']
})
export class SearchProductComponent implements OnInit {
  @ViewChild("inputSearchSales") myInputField: ElementRef;
  @Input("searchProductList") searchProductList: boolean = true;
  @Output("closeSearchProductList") closeSearchProductList = new EventEmitter<any>();
  observableProducts: any;
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  indexTab: number = 0
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales
  ) { }
  public goodList: any[] = [];
  public searchList: any[] = [];
  public typeGoods = 0;
  search: string = '';
  ngOnInit(): void {
    this.changeTypeGood(this.typeGoods);
  }
  ngOnDestroy() {
    this.observableProducts.unsubscribe();
  }
  ngAfterViewInit() {
    this.myInputField.nativeElement.focus();
  }
  acticeFocus() {
    this.myInputField.nativeElement.focus();
  }
  goBack() {
    this.closeSearchProductList.emit(false);
  }
  tempVal: string = ''
  searchProduct(value: string) {
    this.tempVal = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(this.tempVal, this.goodList, ['name', 'barcode'])
    else this.searchList = this.goodList
  }
  chooseProduct(item) {
    this.closeSearchProductList.emit(item)
  }
  tabs: any = ['Product', 'Service', 'Combo']
  changeTypeGood(event) {
    if (this.observableProducts) this.observableProducts.unsubscribe();

    let type: number = VhType.PRODUCT;
    switch (event) {
      case 0: type = VhType.PRODUCT; break;
      case 1: type = VhType.SERVICE; break;
      default: type = VhType.COMBO; break;
    }

    this.observableProducts = this.vhQuerySales.getObservableLocalProducts(type).subscribe((localProducts: any) => {
      this.goodList = localProducts.filter(good => {
        if (type == VhType.COMBO) good.subComboName = this.renderSubCombo(good.combos)
          return true
      });
      this.searchList = this.goodList;
      this.searchProduct(this.tempVal)
    })
  }

  renderSubCombo(arr: any[] = []) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }
}
